.new {
  margin-bottom: 5rem;

  &__title {
    margin: 5rem 0 2.5rem;
  }

  &__carousel.slick-slider {
    margin: 0 -1rem;

    .slick-track {
      padding: 0 0 2rem 0;

      .carousel-item__wrapper {
        padding: 0 1rem;
      }
    }

    .slick-dots {
      bottom: -18px;
    }

    .product-item {
      max-height: 32rem;
    }

    .slick-prev {
      left: 0;
    }

    .slick-next {
      right: 0;
    }
  }
}